.howtobuy {
  color: $textColor;

  &__title {
    margin: 0 0 32px;
    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
    letter-spacing: -1.5px;

    @include onDesktop {
      font-size: 48px;
      line-height: 48px;
      margin-bottom: 0;
    }

    &:hover {
      @include onDesktop {
        transition: transform 0.5s;
        transform: scale(1.2);
      }
    }
  }

  &__subtitle {
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    align-self: stretch;
    margin-bottom: 16px;
  }

  &__paragraph {
    margin-top: 40px;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;

  }
}
